import Vue from 'vue'
import Router from 'vue-router'

/**
 * 路由配置
 */
// 电影模块
import Movie from './movie'
import Upcoming from './movie/upcoming'

// 影院模块
import Cinema from './cinema'

// 我的模块
import Mine from './mine'

// pubilc
import Location from './public/location'
import Search from './public/search'
import Details from './public/details'

Vue.use(Router)

export default new Router({
    // history 
    mode: 'hash',
    base: process.env.BASE_URL,
    routes: [
        Movie, // 电影
        Upcoming, // 电影 - 即将上映
        Cinema, // 影院
        Mine, // 我的
        Location, // 位置
        Search, // 搜索
        Details, // 详情
        { path: '/', redirect: '/movie' },
        // {
        //   path: '/movie/location',
        //   name: 'location',
        //   component: () => import('@/components/location')
        // },
    ]
})